/* eslint-disable react/prop-types */
/* eslint-disable react/display-name */
/**
 * System imports
 ============================================================================ */
import React from "react";
import {
  StoreOutlined,
  AccountBalanceOutlined,
  FaceOutlined,
  MoneyOffOutlined,
} from "@material-ui/icons";

import Card from "../../../card";
// import Icon from "../../../icon";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "Toasts" };

  /**
     * Properties
     ============================================================================ */
  // eslint-disable-next-line no-unused-vars
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Return
     ============================================================================ */
  return (
    <div className="bd-masthead">
      <Card
        backgroundHeader="Transparent"
        backgroundBody="Transparent"
        border="0"
        align="center"
        header={false}
      >
        <AccountBalanceOutlined
          style={{ fontSize: "5rem", color: "var(--primary)" }}
        />
        <p className="toast-caption" style={{ color: "var(--primary)" }}>
          One Click Allowance
        </p>
        <p className="toast-content">
          Sign up to receive a welcome boost. Sign in daily to cash in more.
        </p>

        <FaceOutlined style={{ fontSize: "5rem", color: "var(--primary)" }} />
        <p className="toast-caption" style={{ color: "var(--primary)" }}>
          Peer Shares
        </p>
        <p className="toast-content">
          Allows all the value creators to share in revenues, including
          micropayments.
        </p>

        <StoreOutlined style={{ fontSize: "5rem", color: "var(--primary)" }} />
        <p className="toast-caption" style={{ color: "var(--primary)" }}>
          Business Toolbox
        </p>
        <p className="toast-content">
          Supports product codes, dynamic activation, and more.
        </p>

        <MoneyOffOutlined
          style={{ fontSize: "5rem", color: "var(--primary)" }}
        />
        <p className="toast-caption" style={{ color: "var(--primary)" }}>
          Local Currencies Only
        </p>
        <p className="toast-content">
          We believe in evenly distributed value and fast spending.
        </p>
      </Card>
    </div>
  );
};
